import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styles from './PasswordButton.module.css';
import { valida } from '../store/button';

const PasswordButton = () => {
  const dispatch = useDispatch();
  const { button } = useSelector((state) => state);

  function handleClick() {
    dispatch(valida(button));
  }

  if (button)
    return (
      <React.Fragment>
        <div className="dica anime">
          <p>Usuário: dog</p>
          <p>Senha: dog</p>
        </div>
        <button onClick={handleClick} className={`${styles.button} anime`}>
          Fecha-te Sésamo
        </button>
      </React.Fragment>
    );
  return (
    <button onClick={handleClick} className={`${styles.button} anime`}>
      Abre-te Sésamo
    </button>
  );
};

export default PasswordButton;
