import { createSlice } from '@reduxjs/toolkit';

const slice = createSlice({
  name: 'button',
  initialState: false,
  reducers: {
    abrir: () => true,
    fechar: () => false,
  },
});

const { abrir, fechar } = slice.actions;

export const valida = (payload) => (dispatch) => {
  if (payload) dispatch(fechar());
  else dispatch(abrir());
};
export default slice.reducer;
